.page {
  &:has(.page__menu:target) {
    overflow: hidden; /* not to scroll the page */
  }

  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: $text-color;
  background-color: #1d1d1d;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transition: all 0.4s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
